import * as React from "react"
import { Link } from "gatsby"
import styled from "styled-components"
import {FaBars} from "react-icons/fa"
import {menuData} from "../data/MenuData"
import { css } from "styled-components"
import {AiOutlineGithub, AiFillLinkedin} from "react-icons/ai"

const iconsLink = [
  {
      icon: (<AiOutlineGithub/>),
      link: "http://github.swordmaster.tech/"
  },
  {
      icon: (<AiFillLinkedin/>),
      link: "http://linkedin.swordmaster.tech/"
  }
]

const Header = ({props}) => {
  return(
    <Nav>
      <Bars />
      <NavMenu>
        {menuData.map((item, index) => (
          <NavLink to={item.link} key={index}>
            {item.title}
          </NavLink> 
        ))}
      </NavMenu>
      <NavBtn>
      {iconsLink.map((item, index) => (
        <NavLinkIcon to={item.link} key={index}>
          {item.icon}
        </NavLinkIcon> 
      ))}
      </NavBtn>
    </Nav>
  )
}



export default Header

const Nav = styled.nav`
  background: #222831;
  height: 80px;
  display: flex;
  justify-content: space-between;
  padding: 0.5rem calc((100vw - 1300px) / 2);
  z-index: 100;
  position: relative;

  ${props => props.secondary && css`
    background: black;
  `}
`
// The link to each of the pages
const NavLink = styled(Link)`
  color: #fff;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0 1rem;
  height: 100%;
  cursor: pointer;
  
`

// The link to each of the pages
const NavLinkIcon = styled(Link)`
  color: #fff;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0 .5rem;
  font-size: 1.5rem;
  height: 100%;
  cursor: pointer;
  
`

// Bar styling for mobile device
// Appears if width < 768 pixels
const Bars = styled(FaBars)`
  display: none;
  color: #fff;

  @media screen and (max-width: 768px){
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 75%);
    font-size: 1.8rem;
    cursor: pointer;
  }
`
// The implementation of navigation menu
// Hides when user is on mobile
const NavMenu = styled.div`
  display: flex;
  align-items: center;
  margin-right: -48px;

  @media screen and (max-width: 768px){
    display: none;
  }
`
const NavBtn = styled.div`
  display: flex;
  align-items: center;
  margin-right: 24px;

  @media screen and (max-width: 768px){
    display: none;
  }
`